<template>
  <div class="decision-grail">
    <el-card>
      <div slot="header" class="card-title">
        <span>大盘分析
          <el-popover placement="right" width="300" trigger="hover">
            <div>
              <p>针对产品决策流分析。</p>
            </div>
            <i class="el-icon-question" slot="reference"></i>
          </el-popover>
        </span>
      </div>
      <el-form :inline="true">
        <div>
          <el-radio-group v-model="callFrom" size="small" fill="#409eff">
            <el-radio-button label="卡卡贷"></el-radio-button>
            <el-radio-button label="豆豆钱"></el-radio-button>
          </el-radio-group>
        </div>
        <el-switch v-model="isContrast" active-text="对比" inactive-text="单一" active-color="#409eff"></el-switch>
        <div v-show="!isContrast" style="padding-top: 20px; padding-bottom: 20px; padding-left: 100px;">
        <datepicker :init-dates.sync="createTime" v-on:update:date="newV => createTime = newV"></datepicker>
        </div>
        <div v-show="isContrast" style="height: 80px;">
          <datepicker label="时间段1" :init-dates.sync="createTimeInterval1" v-on:update:date="upDateEnvDateInterval1"></datepicker>
          <datepicker label="时间段2" :init-dates.sync="createTimeInterval2" v-on:update:date="upDateEnvDateInterval2"></datepicker>
        </div>
          <div>
            <el-button type="primary" @click.prevent.stop="queryTree">查询</el-button>
          </div>
      </el-form>
      <span class="nodata el-table__empty-block" v-if="treeDataList.length === 0">暂无数据</span>
      <div v-for="(treeData, i) in treeDataList" :key="i">
        <div class="chartTooltip hidden" :id="`chartTooltip${i}`">
          <p  v-show="!isCurrentContrast" class="count"></p>
          <p  v-show="!isCurrentContrast" class="rate"></p>
          <table v-show="isCurrentContrast">
            <thead>
              <tr>
                <th></th>
                <th class="contrastTitle1">时间段1&nbsp;</th>
                <th class="contrastTitle2">时间段2</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>总数：</td>
                <td class="contrastCount1"></td>
                <td class="contrastCount2"></td>
              </tr>
              <tr>
                <td>通过率：</td>
                <td class="contrastRate1"></td>
                <td class="contrastRate2"></td>
              </tr>
            </tbody>
          </table>
        </div>
        <svg :id="`tree-container${i}`" class="tree-container"></svg>
      </div>
    </el-card>

    <dialog-rule :init-visible.sync="showDialogRule" ref="dialogRule"></dialog-rule>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import datepicker from '@/components/datepicker';
import dialogRule from '@/components/dialog-rule';
import {getDecisionTree, getDecisionTreeInterval} from '@/api/decision';
import {drawTree} from '@/assets/scripts/svgTree.js';

export default {
  name: 'decision-grail',
  components: {datepicker, dialogRule},
  data() {
    return {
      callFrom: '卡卡贷',
      moduleType: undefined,
      createTime: [
        dayjs()
          .add(-1, 'day')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD')
      ],
      createTimeInterval1: [
        dayjs()
          .add(-1, 'day')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD')
      ],
      createTimeInterval2: [
        dayjs()
          .add(-3, 'day')
          .format('YYYY-MM-DD'),
        dayjs()
          .add(-2, 'day')
          .format('YYYY-MM-DD')
      ],
      treeDataList: [],
      showDialogRule: false,
      isContrast: false,
      isCurrentContrast: false
    };
  },
  watch: {
    callFrom: function() {
      if (this.isCurrentContrast) {
        this.getDecisionTreeInterval();
      } else {
        this.getDecisionTree();
      }
    },
    moduleType: function() {
      if (this.isCurrentContrast) {
        this.getDecisionTreeInterval();
      } else {
        this.getDecisionTree();
      }
    }
    /*createTime: function() {
      if (this.isCurrentContrast) {
        this.getDecisionTreeInterval();
      } else {
        this.getDecisionTree();
      }
    },
    unContrast: function() {
      if (this.unContrast) {
        this.getDecisionTree();
      } else {
        this.getDecisionTreeInterval();
      }
    }*/
  },
  created() {
    if (this.isCurrentContrast) {
      this.getDecisionTreeInterval();
    } else {
      this.getDecisionTree();
    }
  },
  mounted() {},
  methods: {
    getDecisionTree() {
      this.$store.commit('UPDATE_TREECONTRACT', this.isCurrentContrast);
      this.$store.commit('UPDATE_CONCTRASTDATERANGE', []);
      getDecisionTree(
        this.callFrom,
        this.createTime[0],
        this.createTime[1],
        this.moduleType
      )
        .then(result => {
          if (Array.isArray(result.result)) {
            this.treeDataList = result.result;
            this.$nextTick(() => {
              for (let i = 0; i < this.treeDataList.length; i++) {
                drawTree(`#tree-container${i}`, this.treeDataList[i], i, true, false, [], this.showDialogFunc);
              }
            });
          }
        })
        .catch(err => console.log(err));
    },
    getDecisionTreeInterval() {
      if (
        !(
          this.createTimeInterval1[0] &&
          this.createTimeInterval1[1] &&
          this.createTimeInterval2[0] &&
          this.createTimeInterval2[1]
        )
      ) {
        this.$message({
          message: '请选择查询时间',
          type: 'warning'
        });
        return;
      }
      this.$store.commit('UPDATE_TREECONTRACT', this.isCurrentContrast);
      this.$store.commit('UPDATE_CONCTRASTDATERANGE', [
        this.createTimeInterval1[0] + '&' + this.createTimeInterval1[1],
        this.createTimeInterval2[0] + '&' + this.createTimeInterval2[1]
      ]);
      getDecisionTreeInterval(
        this.callFrom,
        this.createTimeInterval1[0] + '&' + this.createTimeInterval1[1],
        this.createTimeInterval2[0] + '&' + this.createTimeInterval2[1],
        this.moduleType
      )
        .then(result => {
          if (Array.isArray(result.result)) {
            this.treeDataList = result.result;
            let dateRanges = new Array(
              this.createTimeInterval1[0] + '&' + this.createTimeInterval1[1],
              this.createTimeInterval2[0] + '&' + this.createTimeInterval2[1]
            );
            this.$nextTick(() => {
              for (let i = 0; i < this.treeDataList.length; i++) {
                drawTree(`#tree-container${i}`, this.treeDataList[i], i, true, true, dateRanges, this.showDialogFunc);
              }
            });
          }
        })
        .catch(err => console.log(err));
    },
    showDialogFunc(moduleName) {
      this.moduleName = moduleName;
      this.showDialogRule = true;
      if (this.isCurrentContrast) {
        this.$refs.dialogRule.$emit(
          'drawTree',
          this.moduleName,
          this.createTimeInterval1,
          this.createTimeInterval2,
          this.isCurrentContrast
        );
      } else {
        this.$refs.dialogRule.$emit(
          'drawTree',
          this.moduleName,
          this.createTime,
          [],
          this.isCurrentContrast
        );
      }
    },
    upDateEnvDateInterval1(val) {
      this.createTimeInterval1 = val;
    },
    upDateEnvDateInterval2(val) {
      this.createTimeInterval2 = val;
    },
    queryTree() {
      this.isCurrentContrast = this.isContrast;
      if (this.isCurrentContrast) {
        this.getDecisionTreeInterval();
      } else {
        this.getDecisionTree();
      }
    }
  }
};
</script>

<style scoped>
.el-card {
  margin: 20px 15px;
  height: calc(100vh - 112px);
}
.el-card__header {
  font-size: 16px;
}
.card-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.el-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tree-container {
  margin-top: 20px;
  width: 100%;
  height: calc(100vh - 230px);
}
.chartTooltip {
  position: absolute;
  width: 180px;
  height: auto;
  padding: 10px;
  box-sizing: border-box;
  background-color: white;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.4);
  pointer-events: none;
}

.chartTooltip.hidden {
  display: none;
}

.chartTooltip p {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
  word-wrap: break-word;
}

.chartTooltip .contrastCount1 {
  color: #67c23a;
  text-align: center;
}
.chartTooltip .contrastCount2 {
  color: #e6a23c;
  text-align: center;
}
.chartTooltip .contrastRate1 {
  color: #67c23a;
  text-align: center;
}
.chartTooltip .contrastRate2 {
  color: #e6a23c;
  text-align: center;
}

/*.chartTooltip table tr th{
  border-bottom: 1px dashed #78818f;
}*/
.chartTooltip table tr td {
  border-bottom: 1px dashed #78818f;
}
</style>
