<template>
    <el-dialog
            class="children-tree"
            :title="title"
            :visible.sync="dialogVisible"
            width="90%"
            top="10vh"
    >
        <div class="tree-container">
            <el-tree
                    :data="treeData"
                    :props="defaultProps"
                    node-key="id"
                    :default-expanded-keys="[0]"
                    accordion
                    :highlight-current="true"
                    @node-click="handleNodeClick"
            ></el-tree>
        </div>
        <div id="rule-edit-area" v-show="showArea"></div>
    </el-dialog>
</template>

<script>
  import $ from 'jquery';
  import {drawTree} from '@/assets/scripts/svgTree.js';
  import SymbolJson from '@/assets/scripts/symbolList.js';
  import {getRules, getRuleDetail, getRuleCondCount} from '@/api/decision.js';

  //判断是否为新增 1-是 0-否
  var IsNew = 1;
  //是否修改过 1-是 0-否
  var HasEdited = 0;
  //规则组
  var grouprule = '<div class="group" exprid="" top="0"></div>';
  //空规则模板 用于删除
  var emptyrule =
    '<input class="inputstyle ruletext" type="text" value="双击选择" title="" expr="" readonly="readonly" /><span menu="0" class="operation">操作</span><br/>';
  //空规则模板，带parent 用于新增
  var emptyrulewithparent = '<div style="padding-left:5px; padding-right:5px">' + emptyrule + '</div>';
  //新规则增加
  var appendrule = '<span menu="0" class="operation append"></span>';
  //当前树节点
  var CurrentNode = null;
  //当前父节点
  var CurrentParentNode = null;
  var totalWidthTree = 0;
  //符号列表
  var symbolList = [];
  //运算匹配的正则
  var ruleReg = /{\d*?}/g;
  //编辑区域
  var RuleEditArea = null;
  //规则显示方式
  var showAsTree = true;
  var objAdd = {
    text: '条件编辑',
    data: []
  };
  var objDelete = {
    text: '条件删除'
    //   func: function() {
    //     DelEditor($(this));
    //   }
  };

  var ruleMenuData = [[objDelete, objAdd]];
  var ruleAppendData = [[objAdd]];

  const RULE_Symbol = '@R@';
  const VB_Symbol = '@V@';
  const IsEdit = 0;

  export default {
    name: 'children-tree',
    data() {
      return {
        moduleName: '',
        showArea: false,
        treeData: [],
        defaultProps: {
          children: 'children',
          label: 'name'
        }
      };
    },
    props: {
      initVisible: {
        type: Boolean,
        required: true,
        default: false
      }
    },
    computed: {
      title() {
        return `${this.moduleName} 详情`;
      },
      dialogVisible: {
        get: function() {
          return this.initVisible;
        },
        set: function(newV) {
          if (!newV) {
            this.$emit('update:initVisible', newV);
          }
        }
      }
    },
    mounted() {

      this.$on('drawTree',
        (moduleName, date, dateContract = [], isContract = false) => {
          this.date = date;
          this.dateContract = dateContract;
          this.isContract = isContract;
          if (this.moduleName === moduleName) {
            return;
          }
          this.moduleName = moduleName;
          //   this.moduleName = '2018_卡卡-主决策（c1）';
          if (this.moduleName) {
            getRules(this.moduleName)
              .then(result => {
                if (Array.isArray(result.result)) {
                  this.createTree(result.result);
                }
                RuleEditArea = $('#rule-edit-area');
                RuleEditArea.empty();
                emptyrulewithparent =
                  '<div style="margin: 5px;">' + emptyrule + '</div>';
                grouprule = '<div class="grouptree" exprid="" top="0"></div>';
                RuleEditArea.addClass('ruletree');
                RuleEditArea.append('<ul></ul>' + appendrule);
                this.initMenu();
              })
              .catch(err => console.log(err));
          }
        }
      );
    },
    methods: {
      createTree(data) {
        let tree = data.filter(item => item.isroot === 1);
        for (let item of tree) {
          item.id = 0;
          item.children = data.filter(rule => rule.treepId === item.treeid);
          for (let one of item.children) {
            one.children = data.filter(rule => rule.treepId === one.treeid);
          }
        }
        this.treeData = tree;
      },
      initMenu() {
        var json = [];
        objAdd.data.length = 0;
        symbolList.length = 0;
        totalWidthTree = 0; // 400 - RuleEditArea.width();
        SymbolJson.forEach((item, index) => {
          var val = $.extend(true, {}, item);
          var obj = {
            text: val.Description,
            expr: val.Id,
            func: exprid => {
              this.addEditor(exprid, $(this));
              if (showAsTree) {
                var firstUl = RuleEditArea.children('ul:first');
                if (totalWidthTree > firstUl.width()) {
                  firstUl.width(totalWidthTree);
                }
              }
            }
          };
          if (showAsTree) {
            val.ExprHtml = val.ExprHtmlTree;
            switch (val.Id) {
              case 1:
              case 2:
                var exprArr = val.ExprHtml.split('<span>');
                val.ExprHtml =
                  '<div class ="node boolean"><span>' +
                  exprArr[1] +
                  '</div><b class="boolean-before"></b><b class="boolean-after"></b><ul><li><div class="node">' +
                  '<span>' +
                  exprArr[2] +
                  '</div></li></ul>';
                break;
              case 3:
                val.ExprHtml = '<div class ="node">' + val.ExprHtml + '</div>';
                break;
              default:
                break;
            }
          }
          json.push(obj);
          symbolList.push(val);
        });
        objAdd.data.push(json);
      },
      handleNodeClick(data) {
        console.log(data);
        if (data.isParent === 'true') {
          return;
        }
        this.showArea = true;
        getRuleDetail(data.id)
          .then(result => {
            console.log(result);
            let ruleObj = result.result;
            try {
              this.parseRuleEditExpr(ruleObj.ru_editexp);
            } catch (e) {
              console.log(e);
              this.$message.warning('解析规则失败');
            }
            this.queryRuleCondCount(ruleObj.ru_editexp);
            $('.operation').addClass('hide');
          })
          .catch(err => console.log(err));
      },
      queryRuleCondCount(ruleJsonExpr) {
        const exprs = this.getRuleCondCountExprs(ruleJsonExpr);
        if (exprs.length < 1) {
          return;
        }

        this.beforeStr = [];
        this.afterStr = [];

        getRuleCondCount(`2018_${this.moduleName}`, exprs, this.date[0], this.date[1])

          .then(result => {
            let data = result.result;
            if (Array.isArray(data)) {
              let nodes = $('.boolean');
              if (nodes.length > 0) {
                nodes.each((i, node) => {
                  let height = $(node).height();
                  $(node)
                    .find('~b')
                    .css({
                      top: `${height + 36}px`
                    });
                  let count1 = data[i].trueNum; // 是 数量
                  let count2 = data[i].falseNum; // 否 数量
                  let rate1 = ''; // 是 比例
                  let rate2 = ''; // 否 比例
                  if (count1 !== -1 && count2 !== -1) {
                    let total = count1 + count2;
                    if (total === 0) {
                      rate1 = '（0%）';
                      rate2 = '（0%）';
                    } else {
                      rate1 = `（${((count1 / total) * 100).toFixed(2)}%）`;
                      rate2 = `（${((count2 / total) * 100).toFixed(2)}%）`;
                    }
                  }


                  let str1 = count1 === -1 ? '' : `${count1}${rate1 ? rate1 : ''}`;
                  let str2 = count2 === -1 ? '' : `${count2}${rate2 ? rate2 : ''}`;

                  this.beforeStr[i] = str1;
                  this.afterStr[i] = str2;


                  $(node)
                    .parent()
                    .find('.boolean-before')
                    .html(str1);
                  $(node)
                    .parent()
                    .find('.boolean-after')
                    .html(str2);
                });
              }
            } else {
              this.$message.warning('查询数量失败');
            }
            if (this.isContract) {
              this.getContractRuleCondCount(exprs, this.dateContract);
            }
          })
          .catch(err => console.log(err));
      },
      parseRuleEditExpr(ruleJsonExpr) {
        this.clearRule();
        let ruleExprObj = JSON.parse(ruleJsonExpr);
        let operation = RuleEditArea.children('.operation');
        if (ruleJsonExpr.length > 0) {
          console.log(ruleExprObj);
          totalWidthTree = 0;
        }
        if (ruleExprObj.length > 0) {
          $(ruleExprObj).each((index, item) => {
            this.parseRuleHtml(item, operation);
            totalWidthTree =
              totalWidthTree +
              RuleEditArea.find('div.node:last')
                .closest('li')
                .outerWidth();
          });
        }
      },
      parseRuleHtml(ruleObj, operation) {
        if (ruleObj.type == 'rulegroup') {
          var newGroup = this.addEditor(ruleObj.exprid, operation);
          if (ruleObj.children.length > 0) {
            var operationList = null;
            if (!showAsTree) {
              operationList = newGroup.children().children('.operation');
              if (operationList.length == 0) {
                operationList = newGroup
                  .children()
                  .children()
                  .children('.operation');
              }
            } else {
              operationList = newGroup.find('.operation');
            }
            operationList.each((index, item) => {
              this.parseRuleHtml(ruleObj.children[index], $(item));
            });
          }
        } else if (ruleObj.type == 'ruletext') {
          var ruleTextDiv = operation.prev();
          if (ruleObj.textObj) {
            if (ruleObj.textObj.type == 'multi') {
              this.ruleMultiSelectCallback(ruleTextDiv, ruleObj.textObj, 1);
            } else {
              this.ruleSelectCallback(ruleTextDiv, ruleObj.textObj, 1);
            }
          }
        } else {
          this.parseRuleHtml(ruleObj.children[0], operation);
        }
      },
      addEditor(exprid, operation) {
        HasEdited = 1;
        var objSym = this.getExpr(exprid);
        $(objSym.ExprHtml.match(ruleReg)).each(function(index, item) {
          objSym.ExprHtml = objSym.ExprHtml.replace(item, emptyrulewithparent);
        });
        if (operation.hasClass('append')) {
          if (operation.parent().attr('id') != RuleEditArea.attr('id')) {
            console.log(RuleEditArea.attr('id'));
          } else {
            if (!showAsTree) {
              RuleEditArea.children(':last').before(
                $(grouprule)
                  .attr('top', 1)
                  .attr('exprid', exprid)
                  .append($(objSym.ExprHtml))
              );
            } else {
              var element = $(grouprule)
                .attr('top', 1)
                .attr('exprid', exprid)
                .append($(objSym.ExprHtml));
              if (exprid == 18) {
                RuleEditArea.find('ul:last')
                  .append($('<li><div class="node"></div></li>'))
                  .find('.node:last')
                  .append(element);
              } else {
                RuleEditArea.find('ul:last').append($('<li></li>').append(element));
              }
              return element;
            }
          }
          return RuleEditArea.children(':last').prev();
        } else {
          var group = operation.parent();
          group.empty();
          if (showAsTree) {
            //   group.css('border', '1px solid #bbbbbb');
          }
          group.append(
            $(grouprule)
              .attr('exprid', exprid)
              .append($(objSym.ExprHtml))
          );
          return group;
        }
      },
      ruleSelectCallback(selector, selectresult, pageload) {
        var result = selectresult;
        $(selector).removeClass('variable rule custom multi');
        $(selector).addClass(result.type);
        switch (result.type) {
          case 'variable': {
            $(selector).val(result.name);
            $(selector).attr('title', result.name);
            $(selector).attr('expr', result.expr ? result.expr : VB_Symbol + result.id + VB_Symbol);
            break;
          }
          case 'rule': {
            $(selector).val(result.name);
            $(selector).attr('title', result.name);
            $(selector).attr('expr', result.expr ? result.expr : RULE_Symbol + result.id + RULE_Symbol);
            break;
          }
          case 'custom': {
            if ($(selector).parent().parent().attr('exprid') != 18 && !this.isContract) {
              $(selector).on('click', this.showContract);
            }
            if ($.isNumeric(result.name)) {
              $(selector).val(result.name);
              $(selector).attr('title', result.name);
              $(selector).width(this.textWidth($(selector).val()) + 20);
              /*$(selector).parent().parent().append("<el-button type=\"text\" class=\"btn-contract\">对比</el-button>");
              let contractBtn = $(selector).parent().parent().find(".btn-contract");
                      $(contractBtn[0]).on("click", this.contractChange)
              $(selector).parent().parent().append( "<div style='margin: 5px; height: 26px; width: 188px;'><input class=\"inputstyle txt-contract hide\" type=\"text\"  title=\"\" expr=\"\" placeholder='请输入'/> " +
                      "<el-button type=\"success\" class='hide'>确定</el-button></div>");*/
            } else {
              if (!pageload) {
                $(selector).val(
                  '"' +
                  result.name
                    .replace('\'', '‘')
                    .replace('"', '“')
                    .replace(RULE_Symbol, '#V#')
                    .replace(RULE_Symbol, '#R#') +
                  '"'
                );
                $(selector).attr(
                  'title',
                  '"' +
                  result.name
                    .replace('\'', '‘')
                    .replace('"', '“')
                    .replace(RULE_Symbol, '#V#')
                    .replace(RULE_Symbol, '#R#') +
                  '"'
                );
              } else {
                $(selector).val(result.name);
                $(selector).attr('title', result.name);
              }
            }
          }
        }
        $(selector).width(this.textWidth($(selector).val()));
        HasEdited = 1;
      },
      ruleMultiSelectCallback(selector, selectresult, pageload) {
        var result = selectresult;

        $(selector).removeClass('variable rule custom multi');
        $(selector).addClass('multi');

        $(selector).val(result.name);
        $(selector).attr('title', result.name);

        if (result.expr) {
          $(selector).attr('expr', result.expr);
        } else {
          var arr = result.id.split(',');
          $(arr).each(function(index, item) {
            arr[index] = 'Convert.ToDecimal(' + item + ')';
          });
          $(selector).attr('expr', arr.join());
        }
      },
      getExpr(exprid) {
        var result = '';
        $(symbolList).each(function(index, item) {
          if (item.Id == exprid) {
            result = $.extend(true, {}, item);
            return false;
          }
        });
        return result;
      },
      //获取文本宽度
      textWidth(text) {
        var sensor = $('<pre>' + text + '</pre>').css({display: 'none'});
        $('body').append(sensor);
        var width = sensor.width();
        sensor.remove();
        return width;
      },
      clearRule() {
        RuleEditArea.children('ul:first')
          .children()
          .remove();
        RuleEditArea.children('ul:first').width('auto');
      },
      getRuleCondCountExprs(ruleJsonExpr) {
        let ruleExprObj = JSON.parse(ruleJsonExpr);
        const exprs = ruleExprObj
          .map((item, i) => {
            if (Number(item.exprid) === 1 || Number(item.exprid) === 2) {
              return {
                id: i,
                expr: item.children[0].expr
              };
            } else {
              return {};
            }
          })
          .filter(item => 'expr' in item);
        return exprs;
      },
      getContractRuleCondCount(exprs, date) {
        getRuleCondCount(`2018_${this.moduleName}`, exprs, date[0], date[1])
          .then(result => {
            let data = result.result;
            if (Array.isArray(data)) {
              let nodes = $('.boolean');
              if (nodes.length > 0) {
                nodes.each((i, node) => {
                  let height = $(node).height();
                  $(node)
                    .find('~b')
                    .css({
                      top: `${height + 36}px`
                    });
                  let count1 = data[i].trueNum; // 是 数量
                  let count2 = data[i].falseNum; // 否 数量
                  let rate1 = ''; // 是 比例
                  let rate2 = ''; // 否 比例
                  if (count1 !== -1 && count2 !== -1) {
                    let total = count1 + count2;
                    if (total === 0) {
                      rate1 = '（0%）';
                      rate2 = '（0%）';
                    } else {
                      rate1 = `（${((count1 / total) * 100).toFixed(2)}%）`;
                      rate2 = `（${((count2 / total) * 100).toFixed(2)}%）`;
                    }
                  }

                  let str1 =
                    count1 === -1 ? '' : `${count1}${rate1 ? rate1 : ''}`;
                  let str2 =
                    count2 === -1 ? '' : `${count2}${rate2 ? rate2 : ''}`;
                  $(node)
                    .parent()
                    .find('.boolean-before')
                    .html(
                      '<span style=\'color: #67C23A\'>' +
                      this.beforeStr[i] +
                      '</span> , <span style=\'color: #E6A23C\'>' +
                      str1 +
                      '</span>'
                    );
                  $(node)
                    .parent()
                    .find('.boolean-after')
                    .html(
                      '<span style=\'color: #67C23A\'>' +
                      this.afterStr[i] +
                      '</span> , <span style=\'color: #E6A23C\'>' +
                      str2 +
                      '</span>'
                    );
                });
              }
            } else {
              this.$message.warning('查询数量失败');
            }
          })
          .catch(err => console.log(err));
      },
      showContract(event) {
        this.$prompt('请输入对比值', '对比', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        })
          .then(({value}) => {
            if ($.isNumeric(value)) {
              $(event.target).val(value);
              $(event.target).attr('title', value);
            } else {
              $(event.target).val('"' + value + '"');
              $(event.target).attr('title', '"' + value + '"');
            }
            $(event.target).width(this.textWidth($(event.target).val()));
            let ruleJsonExpr = JSON.stringify(this.OrgRule());
            const exprs = this.getRuleCondCountExprs(ruleJsonExpr);
            if (exprs.length < 1) {
              return;
            }
            this.getContractRuleCondCount(exprs, this.date);
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '取消输入'
            });
          });
      },
      OrgRule() {
        var arrRule = new Array();

        var groups = RuleEditArea.children('.group[top=1]');
        if (showAsTree) {
          groups = RuleEditArea.find('.grouptree[top=1]');
        }
        /*for(var item in groups) {
          var rule = this.OrgGroup($(item), -1);
          arrRule.push(rule);
        }*/
        groups.each((index, item) => {
          var rule = this.OrgGroup($(item), -1);
          arrRule.push(rule);
        });

        return arrRule;
      },
      OrgGroup(group, index) {
        var rule = {exprid: 0, children: [], expr: '', textObj: null, type: ''};

        if (
          index == -1 ||
          group.children('.group').length > 0 ||
          group.hasClass('group') ||
          group.children('.grouptree').length > 0 ||
          group.hasClass('grouptree')
        ) {
          if (group.attr('exprid')) {
            rule.type = 'rulegroup';
            rule.exprid = group.attr('exprid');
          }

          rule.children = new Array();
          var i = 0;

          var groupChildren = group.children();
          if (showAsTree) {
            groupChildren = $.merge(
              group.children('.node').children(),
              group.find('ul>li>div.node').children()
            );
            if (groupChildren.length == 0) {
              groupChildren = group.children();
            }
          }
          groupChildren.each((index, item) => {
            if (
              $(item).children('.ruletext').length > 0 ||
              $(item).children('.group').length > 0 ||
              $(item).hasClass('group') ||
              $(item).children('.grouptree').length > 0 ||
              $(item).hasClass('grouptree')
            ) {
              i++;
              rule.children.push(this.OrgGroup($(item), i));
            }
          });
        } else {
          rule.type = 'ruletext';
          var textType = '';
          var ruletext = group.children('.ruletext:first');
          if (ruletext.hasClass('variable')) {
            textType = 'variable';
          } else if (ruletext.hasClass('rule')) {
            textType = 'rule';
          } else if (ruletext.hasClass('custom')) {
            textType = 'custom';
          } else if (ruletext.hasClass('multi')) {
            textType = 'multi';
          }
          rule.textObj = {
            type: textType,
            name: ruletext.val(),
            expr: ruletext.attr('expr') ? ruletext.attr('expr') : ''
          };
        }

        if (rule.exprid > 0) {
          var objSym = this.getExpr(rule.exprid);
          $(objSym.Expr.match(ruleReg)).each(function(index, item) {
            var idx = parseInt(item.replace('{', '').replace('}', ''));
            if (rule.children[idx].type == 'ruletext') {
              if (rule.children[idx].textObj.type == 'custom') {
                objSym.Expr = objSym.Expr.replace(
                  item,
                  rule.children[idx].textObj.name
                );
              } else {
                objSym.Expr = objSym.Expr.replace(
                  item,
                  rule.children[idx].textObj.expr
                );
              }
            } else {
              objSym.Expr = objSym.Expr.replace(item, rule.children[idx].expr);
            }
          });
          rule.expr = objSym.Expr;
        } else {
          if (rule.children.length > 0) {
            var expr = rule.expr;
            $(rule.children).each(function(index, item) {
              expr = expr + item.expr;
            });
            rule.expr = expr;
          }
        }

        return rule;
      }
      /*contractChange(event){
          event.stopPropagation();
          if (event.target.nextSibling.childNodes[0].classList.contains('hide')) {
            event.target.nextSibling.childNodes[0].classList.remove('hide')
            event.target.nextSibling.childNodes[2].classList.remove('hide')
          }else{
            event.target.nextSibling.childNodes[0].classList.add('hide')
            event.target.nextSibling.childNodes[2].classList.add('hide')
          }
          return false;
        }*/
    }
  };
</script>


<style lang="scss">
 .children-tree {
    .el-dialog {
        height: 80vh;
    }
    .el-card__header {
        font-size: 16px;
    }
    .card-title {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .card-title button {
        padding: 0;
    }
    .tree-container {
        float: left;
        max-width: 300px;
        height: calc(80vh - 100px);
        overflow-y: auto;
    .el-tree {
        margin-left: 0;
        width: 300px;
    }
    }

    #rule-edit-area {
        float: left;
        margin-left: 15px;
        border: 1px solid #72c3ec;
        width: calc(100% - 320px);
        height: calc(80vh - 100px);
        overflow: auto;
    ul {
        width: max-content;
    }
    > ul {
        padding-top: 0;
    }
    }

    .group span {
        float: left;
        height: 20px;
        line-height: 20px;
        font-size: 15px;
        font-weight: bold;
        padding-top: 5px;
    }

    .inputstyle {
        width: 150px;
        height: 20px;
        line-height: 20px;
        font: 15px;
        float: left;
        padding-top: 4px;
        border-left: none;
        border-right: none;
        border-top: none;
        border-bottom: 1px solid #666;
    }

    .variable {
        color: #fff;
        background-color: #ffb800;
        width: 150px;
        border: 0px;
        border-radius: 5px;
        margin: 2px;
        padding-top: 0px;
        padding: 2px;
        cursor: pointer;
        font-size: 12px;
        text-align: center;
        max-width: 12em;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .variable:hover {
        /* hover放大 */
        opacity: 0.8;
    }

    .rule {
        color: #fff;
        background-color: #009688;
        width: 150px;
