    /*以下为规则树的css*/
    .ruletree {
    // overflow: auto;
    }

    .ruletree ul {
        padding-top: 20px;
        position: relative;
    > li:first-child:nth-last-child(2) {
        margin-left: -210px;
    }
    }

    .ruletree li {
        float: left;
        text-align: center;
        list-style-type: none;
        position: relative;
        padding: 20px 15px 0 15px;
        position: absolute;
        /*  transition: all 0.5s;
       -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;*/
    }

    /*We will use ::before and ::after to draw the connectors*/

    .ruletree li::before,
    .ruletree li::after {
        content: '';
        position: absolute;
        top: 0;
        right: 50%;
        border-top: 1px solid #ccc;
        width: 50%;
        height: 20px;
    }

    .ruletree li::after {
        right: auto;
        left: 50%;
        border-left: 1px solid #ccc;
    }

    /*We need to remove left-right connectors from elements without
  any siblings*/
    .ruletree li:only-child::after,
    .ruletree li:only-child::before {
        display: none;
    }

    /*Remove space from the top of single children*/
    .ruletree li:only-child {
        padding-top: 20px;
    }
    .ruletree > ul > li {
        left: 50%;
        transform: translate(-50%, 0);
    }

    /*Remove left connector from first child and
  right connector from last child*/
    .ruletree li:first-child::before,
    .ruletree li:last-child::after {
        border: 0 none;
    }
    /*Adding back the vertical connector to the last nodes*/
    .ruletree li:last-child::before {
        border-right: 1px solid #ccc;
        border-radius: 0 5px 0 0;
        -webkit-border-radius: 0 5px 0 0;
        -moz-border-radius: 0 5px 0 0;
    }

    .ruletree li:first-child::after {
        border-radius: 5px 0 0 0;
        -webkit-border-radius: 5px 0 0 0;
        -moz-border-radius: 5px 0 0 0;
    }

    /*Time to add downward connectors from parents*/
    .ruletree ul ul::before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        border-left: 1px solid #ccc;
        width: 0;
        height: 20px;
    }

